export default {
	data() {
		return {
			memberIdNum: '', //会员编号
			transferAmount: '' //转账金额
		};
	},
	onLoad(option) {
		console.log(option);
		this.editId = option.editId;
	},
	methods: {
		// 会员编号
		memberIdChange(newpass) {
			this.memberIdNum = newpass.detail.value;
		},

		// 转账金额
		transferAmountChange(confirm) {
			this.transferAmount = confirm.detail.value;
		},

		// 提交
		transferBtn(newbtn) {
			console.log(newbtn);
		}
	}
};
